.signIn {
  height: 100vh;
  background-image: url('../../img/login_back.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.base-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
  padding: 40px;
  background: rgba(0, 0, 0, 0.7);
  box-sizing: border-box;
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
  border-radius: 10px;
}

.base-container h2 {
  margin: 0% 0% 30px;
  padding: 0%;
  color: #FFF;
  text-align: center;
}

.base-container .inputField {
  position: relative;
}

.base-container p {
  font-size: 9px;
  color: red;
}

.base-container .inputField input {
  width: 100%;
  padding: 10px 0;
  font-size: 16px;
  color: #FFF;
  letter-spacing: 1px;
  margin-bottom: 5px;
  border: none;
  border-bottom: 1px solid #FFF;
  outline: none;
  background: transparent;
}

.base-container .inputField label {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  position: absolute;
  top: 0;
  left: 0;
  padding: 10px 0;
  font-size: 16px;
  color: #FFF;
  pointer-events: none;
  transition: 0.5s;
}

.base-container .inputField input:focus~label, .base-container .inputField input:valid~label {
  top: -18px;
  left: 0;
  color: #03a9f4;
  font-size: 12px;
}

.base-container .signInButton {
  text-align: center;
}

.base-container .signInButton button {
  display: block;
  background: transparent;
  border: none;
  outline: none;
  color: #FFF;
  background: #03a9f0;
  cursor: pointer;
  border-radius: 5px;
  margin-top: 10px;
  width: 100%;
  height: 30px;
}